export type Pokemon = {
  link: string;
  pid: number;
  paldea: number | null;
  kitakami: number | null;
  hisui: number | null;
  nameZh: string;
  nameEn: string;
  nameJp: string;
  altForm: string | null;
  hiddenAbility: string | null;
  types: string[];
  abilities: string[];
  baseStats: number[];
  baseStatsTotal: number;
  EVs: number[];
  source: string;
  genderRatio: number;
  eggGroups: string[];
};

type Move = {
  pid: number;
  nameZh: string;
  nameJp: string;
  nameEn: string;
  type: string;
  category: string;
  power: number;
  accuracy: number;
  PP: number;
  description: string;
};

type LevelingUp = Move & { level: number };

type EggMove = Move & { level: number };

type TM = Move & {
  TMPid: number;
  leaguePoint: number;
  materials: {
    material: string;
    count: number;
    pm: string;
  }[];
};

export type PMMove = LevelingUp | EggMove | TM;

type MoveInfo = {
  levelingUps: LevelingUp[];
  eggMoves: EggMove[];
  TMs: TM[];
};

export type SubPokemon = {
  link: string;
  nameZh: string;
  altForm: string | null;
  types: string[];
  condition: string;
};

type MidEvolve = SubPokemon & {
  to?: SubPokemon[];
};

type StartEvolve = {
  from: SubPokemon;
  to: MidEvolve[];
};

export type FullPokemon = Pokemon & {
  moves: MoveInfo;
  evolves?: StartEvolve;
};

export type PokedexFrom = 'kitakami' | 'paldea' | 'hisui';

export const PokedexList = ['kitakami', 'paldea', 'hisui'];

